html {
    overflow-x: hidden;
}

body {
    font-family: $font-family;
    color: $text-medium-emphasis-color;
    font-size: 16px;
    line-height: 160%;
    -webkit-font-smoothing: antialiased;
    word-break: keep-all;
}

#hero-vid {
    backface-visibility: hidden;
    position: fixed;
    z-index: -2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#filter {
    z-index: -1;
    background: rgba(0, 0, 0, .12);
    /*
    background: #2696DA;
    background-image: -webkit-linear-gradient(45deg, #2696DA, #3DB8D4);
    background-image: -moz-linear-gradient(45deg, #2696DA, #3DB8D4);
    background-image: -ms-linear-gradient(45deg, #2696DA, #3DB8D4);
    background-image: -o-linear-gradient(45deg, #2696DA, #3DB8D4);
    background-image: linear-gradient(45deg, #2696DA, #3DB8D4);
    opacity: .38;
    */
    width: 100%;
    top: 0;
    left: 50%;
    height: 100vh;
    transform: translateX(-50%);
    position: fixed;
    text-align: center;
}

.arrow_down {
    color: #fff;
    font-size: 2.4em;
}

.hero-img {
    margin-bottom: -120px;
}

.header-container {
    z-index: 1000;
    position: absolute;
    top: 32%;
    width: 100%;
    text-align: center;
    color: #fff;
    padding: 0 16px;
}

.header-container h5 {
    color: #fff;
    margin-bottom: 24px;
    font-weight: 400;
}

.header-btn {
