}

.bg-white {
    background: #fff;
    z-index: 1;
}

.bg-grey {
    background: #fafafa;
    z-index: 1;
}

.bg-img .bg-img-base {
    visibility: visible;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background: no-repeat center center;
    background-size: cover;
    z-index: 10;
    opacity: 0;
    -webkit-transition: visibility .225s ease, opacity .225s ease-in-out;
    transition: visibility .225s ease, opacity .225s ease-in-out;
}

.bg-img .bg-img-base.in {
    visibility: visible;
    opacity: 1;
}

